// keep aspect ratio of image
.img-wrap {
	position: relative;
	background-color: $black;

	&.-ratio-16-9 {
		padding-bottom: 56.25%;
	}

	&.-ratio-4-3 {
		padding-bottom: 75%;
	}

	&.-ratio-1-1 {
		padding-bottom: 100%;
	}

	& > img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

// responsive background image
.-resp-bg-img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	@include rbg;
}

// background color for embed video
.embed-responsive {
	background-color: $black;
}

// vertical text top - bottom
.-vertical-text-top-bottom {
	writing-mode: vertical-rl;
	-webkit-writing-mode: vertical-rl;
	display: inline-block;
	margin: 0;
}

// vertical text bottom - top
.-vertical-text-bottom-top {
	writing-mode: vertical-rl;
	-webkit-writing-mode: vertical-rl;
	transform: rotate(180deg);
	display: inline-block;
	margin: 0;
}

// visually hidden element
.visually-hidden {
	position: absolute;
	z-index: -1;
	width: 0;
	height: 0;
	padding: 0;
	margin: 0;
	border: none;
	overflow: hidden;
}

// overflow hidden
.-overflow-hidden {
	overflow: hidden;
}

// text overflow
.-text-overflow {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.-space-between-slides-30-pixels {
	.slick-slide {
		margin: 0 15px;
	}

	.slick-list {
		margin: 0 -15px;
	}
}